import {Table} from "antd";
import {useEffect, useState} from "react";
import "./RolesTable.css";
import {useDispatch} from "react-redux";



const RolesTable = (props) => {

    const [tableData, setTableData] = useState([])
    const dispatch = useDispatch()

    const handleOpenModal = (e) => {
        dispatch({ type: 'openModal', roleId: e.target.dataset.id });
    }


    const columns = [
        {
            title: '',
            dataIndex: 'color',
            key: 'color',
            className: 'ant-table-cell--color',
            render(text, record) {
                return {
                    props: {
                        style: {
                            backgroundColor: record.bgColor[0],
                            background: `linear-gradient(135deg, ${record.bgColor[0]} 40%, ${record.bgColor[1]} 60%)`
                        },
                    },
                };
            },
        },
        {
            title: '',
            dataIndex: 'icon',
            key: 'icon',
            className: 'ant-table-cell--icon',
            render(text, record) {
                return {
                    children:
                        <div className={"roles-icons"}>
                            {record.icons.map(function (icon, index) {
                                return (
                                    <img key={index} className="role-icon" alt="" src={`${process.env.PUBLIC_URL}/img/${icon}`} />
                                    )
                            })}
                        </div>
                }
            },
        },
        {
            title: 'Игрок',
            dataIndex: 'player',
            key: 'player',
            render(text, record) {
                return {
                    children: <div>{text}</div>,
                };
            },
        },
        {
            title: 'Роль',
            dataIndex: 'roles',
            key: 'roles',
            render: (text, record) => {
                return {
                    children:
                        <div className={'roles-list'}>
                            {record.roles.map((role, index) => {
                                return (
                                    <>
                                    <span key={index} className='roleLink' data-id={role.dataRole}
                                          onClick={handleOpenModal}>{role.role}{record.roles.length > 1 && index === 0 ? ',': ''}</span>
                                    <space> </space>
                                    </>
                                )
                            })}
                        </div>
                }
            }
        },
        {
            title: 'Количество карт',
            dataIndex: 'cardNum',
            key: 'cardNum',
            render(text, record) {
                return {
                    children: <div>{text}</div>,
                };
            },
        },
        {
            title: 'Число для кубиков',
            dataIndex: 'diesNums',
            key: 'diesNums',
            render(text, record) {
                return {
                    children:
                        <div className={'dies-num'}>
                            {
                                record.diesNums.map(function (num, index) {
                                    return (
                                        <span key={index}>{num}</span>
                                    )
                                })
                            }
                        </div>
                }
            }
        },
        {
            title: 'Возвращения',
            dataIndex: 'resNum',
            key: 'resNum',
            render(text, record) {
                return {
                    children: <div>{text}</div>,
                };
            },
        },
    ]

    useEffect(() => {
        if (tableData.length === 0) {
            let data = [];
            props.players.forEach((player) => {
                let roleQty = 1;
                if (props.gameModes[0].isOn) {
                    roleQty = 2;
                }
                if (player.checked) {
                    let randomRole;
                    let firstRandomRole;
                    let roles = [];
                    let bgColors = [];
                    let icons = [];
                    let diesNums = [];
                    for (let i = 0; i < roleQty; i++) {
                        randomRole = props.roles[props.getRandomInt(0, props.roles.length)];
                        bgColors.push(randomRole.bgColor);
                        icons.push(randomRole.icon)
                        if (i === 0) {
                            firstRandomRole = randomRole;
                        }
                        let role = {
                            role: randomRole.name,
                            dataRole: randomRole.id,
                            color: randomRole.color,
                        }
                        roles.push(role);
                    }
                    for (let i = 0; i < 1; i++) {
                        let diesNum = props.getRandomInt(1, 13);
                        diesNums.push(diesNum);
                    }
                    console.log(bgColors);
                    const newDataEntity = {
                        key: player.id,
                        player: player.label,
                        roles: roles,
                        bgColor: bgColors,
                        icons: icons,
                        diesNums: diesNums,
                        color: firstRandomRole.color,
                        cardNum: (firstRandomRole.id === 0 || randomRole.id === 0) ? 9 : props.getRandomInt(5, 8),
                        resNum: (firstRandomRole.id === 0 || randomRole.id === 0)  ? 0 : props.getRandomInt(1, 3),
                    }

                    data.push(newDataEntity);
                }
            })
            setTableData(data)
        }
    }, [props, tableData.length])

    return (
        <Table pagination={false} columns={columns} dataSource={tableData}/>
    )
}

export default RolesTable;